  }
  .setting-item:hover {
    background: rgba(0, 0, 0, 0.02);
    border-radius: 4px;
  }

  /*  */
  .setting-item input[type="range"],
  .setting-item select {
    width: 100%;
    max-width: 300px;
  }

  .btn-group {
    display: flex;
